const hexagrams8 = require('../hexagram/hexagram_8.json');
const hexagrams64 = require('../hexagram/hexagram_64.json');

export const getTrigram = (num) => {
  const remainder = num % 8;
  const trigramMapping = ["", "", "", "", "", "", "", ""];
  return trigramMapping[(remainder || 8) - 1];
};

export const getHexagram = (lower, upper) => {
  return hexagrams64.find(h => h.lowerTrigram === lower && h.upperTrigram === upper);
};

export const getBinaryFromTrigram = (trigram) => {
  return hexagrams8[trigram].binary;
};

export const calculateResult = async ({
  question,
  xiagua,
  shanggua,
  bianyao,
  setResult,
  setAiInterpretation
}) => {
  try {
    // 
    const lowerTrigram = getTrigram(parseInt(xiagua));
    const upperTrigram = getTrigram(parseInt(shanggua));
    const originalHexagram = getHexagram(lowerTrigram, upperTrigram);

    // 
    const binaryString = getBinaryFromTrigram(lowerTrigram) + getBinaryFromTrigram(upperTrigram);
    const changedPosition = bianyao % 6 || 6;
    let changedBinary = binaryString.split("");
    changedBinary[changedPosition - 1] = changedBinary[changedPosition - 1] === "1" ? "0" : "1";
    changedBinary = changedBinary.join("");

    const changedLowerTrigram = Object.keys(hexagrams8).find(key => 
      hexagrams8[key].binary === changedBinary.slice(0, 3)
    );
    const changedUpperTrigram = Object.keys(hexagrams8).find(key => 
      hexagrams8[key].binary === changedBinary.slice(3)
    );
    const changedHexagram = getHexagram(changedLowerTrigram, changedUpperTrigram);

    //  HTML
    const resultHTML = `
      <div class="space-y-6">
        <div class="bg-gray-50 p-4 rounded-lg">
          <h3 class="text-xl font-semibold mb-2"></h3>
          <p>${question}</p>
        </div>

        <div class="bg-gray-50 p-4 rounded-lg">
          <h3 class="text-xl font-semibold mb-4">${originalHexagram ? originalHexagram.name : ""}</h3>
          <div class="space-y-2">
            <p class="text-2xl">${hexagrams8[upperTrigram].icon}${upperTrigram}</p>
            <p class="text-2xl">${hexagrams8[lowerTrigram].icon}${lowerTrigram}</p>
            <p>${hexagrams8[upperTrigram].property}/${hexagrams8[lowerTrigram].property}</p>
            <p>${hexagrams8[upperTrigram].color}/${hexagrams8[lowerTrigram].color}</p>
            <p>${hexagrams8[upperTrigram].role}/${hexagrams8[lowerTrigram].role}</p>
            <p class="mt-4">${originalHexagram ? originalHexagram.meaning : ""}</p>
          </div>
        </div>

        <div class="bg-gray-50 p-4 rounded-lg">
          <h3 class="text-xl font-semibold mb-4"></h3>
          <p class="mb-4"> ${changedPosition} </p>
          <div class="space-y-2">
            <h4 class="text-lg font-medium">${changedHexagram ? changedHexagram.name : ""}</h4>
            <p class="text-2xl">${hexagrams8[changedUpperTrigram].icon}${changedUpperTrigram}</p>
            <p class="text-2xl">${hexagrams8[changedLowerTrigram].icon}${changedLowerTrigram}</p>
            <p>${hexagrams8[changedUpperTrigram].property}/${hexagrams8[changedLowerTrigram].property}</p>
            <p>${hexagrams8[changedUpperTrigram].color}/${hexagrams8[changedLowerTrigram].color}</p>
            <p>${hexagrams8[changedUpperTrigram].role}/${hexagrams8[changedLowerTrigram].role}</p>
            <p class="mt-4">${changedHexagram ? changedHexagram.meaning : ""}</p>
          </div>
        </div>
      </div>
    `;

    // 
    setResult(resultHTML);

    //  AI 
    try {
      if (window.gemini) {
        const prompt = `

${question}

${originalHexagram.name}
${originalHexagram.meaning}

${changedHexagram.name}
${changedHexagram.meaning}


1. 
2. 
3. 

`;

        const result = await window.gemini.generateText({
          prompt: prompt,
          max_output_tokens: 800,
        });

        setAiInterpretation(result.response.text());
      }
    } catch (error) {
      console.error('AI interpretation error:', error);
      setAiInterpretation("AI ");
    }

    return true;
  } catch (error) {
    console.error('Calculation error:', error);
    throw new Error('');
  }
}; 