import { motion } from 'framer-motion';

export const QuestionStep = ({ question, setQuestion, onNext }) => (
  <motion.div
    key="step1"
    initial={{ opacity: 0, y: 20 }}
    animate={{ opacity: 1, y: 0 }}
    exit={{ opacity: 0, y: -20 }}
    transition={{ duration: 0.3 }}
    className="mb-6"
  >
    <div className="mb-4 p-4 bg-gray-50 rounded-lg text-sm">
      <h3 className="font-medium mb-2"></h3>
      <ol className="list-decimal pl-5 space-y-1">
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ol>
    </div>
    
    <label className="block mb-2 font-medium"></label>
    <textarea 
      value={question} 
      onChange={(e) => setQuestion(e.target.value)} 
      placeholder=""
      className="w-full p-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
    />
    <div className="flex justify-end mt-4">
      <button 
        onClick={onNext}
        disabled={!question.trim()}
        className="px-6 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 disabled:bg-gray-300 disabled:cursor-not-allowed"
      >
        
      </button>
    </div>
  </motion.div>
); 